.lang__items {
  display: flex;
}
.lang__item {
  font-size: 1.5rem;
  line-height: 1.75rem;
  cursor: pointer;
  opacity: 0.2;
  transition: all 0.4s ease-in-out;
}
.lang__item + .lang__item {
  margin-left: 1.5rem;
}
.lang__item--active {
  opacity: 1;
}
.lang__item:hover {
  opacity: 0.6;
}

.header-bottom__menu {
  background-color: #00c1b6;
}

.menu {
  max-width: 68.25rem;
  padding: 0 0.9375rem;
  margin: 0 auto;
}
.menu__list {
  display: flex;
  align-items: center;
  height: 66px;
}
.menu__list-item {
  position: relative;
  padding: 0 1.25rem;
  color: #fbfbfb;
  text-align: center;
  text-transform: uppercase;
}
.menu__list-item::before {
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 50%;
  width: 0.0625rem;
  height: 3.125rem;
  content: "";
  background-color: #fbfbfb;
  transform: translateY(-50%);
